import React, { useState } from "react";
import "./reader.css";

window.sessionStorage.setItem("storyPath", "gbl_chunked");

const get_json = async () =>
  await fetch("fic_html/gbl_chunked/sitemap.json").then(resp => resp.json());

let raw_json = get_json();
let chapters = raw_json.then(resp => Object.entries(resp)[1][1]);

let chapter_list = [];
const get_chaps = async () => await chapters.then(
  resp => {
    const chapter_array = [];
    resp.map(chap => chapter_array.push(chap['section']['path']));
    chapter_list = chapter_list.concat(chapter_array);
  }
)
await get_chaps()
let currentChapter = 0;

function Reader({ story }) {
  const [isNotFirstChapter, setPreviousButtonVis] = useState(false);
  const [isNotLastChapter, setNextButtonVis] = useState(true);

  async function next_chapter() {
    currentChapter++;
    document.getElementById("reader-iframe").src = "fic_html/" + story + "/" + chapter_list[currentChapter];
    setNextButtonVis(currentChapter < chapter_list.length - 1);
    setPreviousButtonVis(true);
  }

  async function previous_chapter() {
    currentChapter--;
    document.getElementById("reader-iframe").src = "fic_html/" + story + "/" + chapter_list[currentChapter]
    setNextButtonVis(true)
    setPreviousButtonVis(currentChapter !== 0)
  }

  return (
    <div>
      <div id="reader">
        <iframe id="reader-iframe" title="reader-iframe" innerHTML={<p></p>} src={"fic_html/" + story + "/" + chapter_list[0]}></iframe>
      </div>
      {(isNotLastChapter) && <button onClick={next_chapter}>Next Chapter</button>}
      {(isNotFirstChapter) && <button onClick={previous_chapter}>Previous Chapter</button>}
    </div>
  )
}

export default Reader;